import React, { useMemo } from 'react';
import { useParams, Redirect } from 'react-router-dom';
import { getHeroById } from '../../selectors/getHeroById';

export const HeroScreen = ( {history} ) => {

    const {heroeId} = useParams();

    const hero = useMemo(() => getHeroById(heroeId), [heroeId])

    if (!hero) {
        return <Redirect to="/" />
    }

    const handleReturn = () => {

        if ( history.length <=2 ) {
            history.push("/");
        } else
        history.goBack();
    }

    const { id, superhero, publisher, alter_ego, first_appearance, characters, } = hero;
    

    return (
        <div className="row mt-5 flexC_m" >
            <div className="col-4 maxW100_m">
                <img
                    src={ `./assets/heroes/${id}.jpg` }
                    
                    alt="heroImgBroken"
                    className="img-thumbnail"
                />
            </div>

            <div className="col-8 maxW100_m t-alignL_m">
                <h3 className="t-alignC_m">{superhero}</h3>
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"> <strong>Alter ego: </strong> {alter_ego} </li>
                    <li className="list-group-item"> <strong>Publisher: </strong> {publisher} </li>
                    <li className="list-group-item"> <strong>First Appearence: </strong> {first_appearance} </li>
                </ul>

                <h5 className="pL">Charecters</h5>
                <p className="pL">{characters}</p>

                <button
                    className="btn btn-outline-info mL"
                    onClick={handleReturn}    
                >
                    Return
                </button>


            </div>



        </div>
    )
}
