import React, { useMemo } from 'react'
import queryString from 'query-string'
import { heroes } from '../../data/heroes'
import { HeroCard } from '../heroes/HeroCard';
import { useForm } from '../../hooks/useForm';
import { useLocation } from 'react-router-dom';
import { getHeroesByName } from '../../selectors/getHeroesByName';


export const SearchScreen = ({history}) => {


    const location = useLocation();

    const {q = ''} = (queryString.parse(location.search))

    const [ formValues, handleInputChange ] = useForm({
        searchText: q
    });

    const { searchText } = formValues

    const heroesFiltered = useMemo(() => getHeroesByName(q), [q])

    const handleSearch = (e) => {
        e.preventDefault();
        history.push(`?q=${searchText}`)
        // console.log(searchText);
    }

    return (
        <div className="mx-5 mt-3">
            <h1>Search Screen</h1>
            <hr />

            <div className="row flexC_m">
                <div className="col-5 maxW100_m">
                    <h4>Search Form</h4>
                    <hr />

                    <form onSubmit={handleSearch}>
                        <input 
                            type="text"
                            placeholder="Find your hero"
                            className="form-control"
                            name="searchText"
                            value={searchText}
                            onChange={handleInputChange}                        
                        />

                        <button
                            type="submit"
                            className="btn btn-block btn-outline-primary mt-3"
                        >
                            Search
                        </button>
                    </form>
                </div>

                <div className="col-7 maxW100_m">
                    <h4 className="mt-3_m">Results</h4>
                    <hr />

                    {
                        ( q === "" ) && <div className="alert alert-info">
                            Search for a hero
                        </div> 
                    }

                    {
                        ( q != "" ) &&  heroesFiltered.length === 0 && <div className="alert alert-info">
                            There is no hero with { q }
                        </div> 
                    }

                    {
                        heroesFiltered.map( hero => (
                            <HeroCard
                                key = { hero.id }
                                {...hero}
                            />
                        ))
                    }

                </div>
            </div>
        </div>
    )
}
